function Intro() {
  return (
    <>
      This sentiment analyzer uses a <i>sentiment</i>. A <b>Sentiment</b> is a
      Node.js module that uses the AFINN-165 wordlist and Emoji Sentiment
      Ranking to perform sentiment analysis on arbitrary blocks of input text.<br />
      For more&nbsp;
      <a href={"https://github.com/thisandagain/sentiment"} target="_blank" rel="noopener noreferrer">information</a>.
    </>
  );
}

export default Intro;
